.tag-span {
  display: inline-block;
  background: rgba(7, 17, 27, 0.05);
  border-radius: 12px;
  padding: 6px 16px;
  color: #4D555D;
  cursor: pointer;
  margin: 8px 8px 0 0;
  -webkit-transition: all 0.3;
  -o-transition: all 0.3;
  transition: all 0.3; }

.checked-color {
  -webkit-transition: all 0.3;
  -o-transition: all 0.3;
  transition: all 0.3;
  background: red;
  color: white; }



