.course-preview {
  width: 216px;
  margin-bottom: 20px; }
  .course-preview a {
    height: 100%;
    display: block; }
    .course-preview a img {
      width: 216px;
      height: 120px;
      border-radius: 8px;
      -webkit-transition: -webkit-box-shadow .3s;
      transition: -webkit-box-shadow .3s;
      -o-transition: box-shadow .3s;
      transition: box-shadow .3s;
      transition: box-shadow .3s, -webkit-box-shadow .3s; }
    .course-preview a h3 {
      width: 210px;
      font-size: 16px;
      color: #07111B;
      line-height: 24px;
      overflow: hidden;
      -o-text-overflow: ellipsis;
         text-overflow: ellipsis;
      max-height: 46px;
      -webkit-transition: color .3s;
      -o-transition: color .3s;
      transition: color .3s;
      padding: 0 8px;
      margin-top: 12px;
      margin-bottom: 0;
      white-space: nowrap;
      text-align: left; }
    .course-preview a .course-preview-data {
      padding: 0 8px;
      margin-top: 10px; }
      .course-preview a .course-preview-data span {
        color: #93999F;
        font-size: 12px;
        margin-right: 16px; }
    .course-preview a .course-preview-price {
      padding: 0 8px;
      margin-top: 4px; }
      .course-preview a .course-preview-price span {
        color: #93999F;
        margin-left: 8px; }



